.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}
.dialogs img {
  width: 70px;
  height: 70px;
  border-radius: 35px;
}

.dialogsItems {
  padding: 10px;
  color: white;
}

.dialogsItems .active {
  color: gold;
}

.messages {
  padding: 10px;
}

.message {
  padding: 10px;
  background-color: rgb(184, 155, 211);
}
